import cheetah from '../../../assets/sprites/cheetah/cheetah.png';
import api from '../../../services/api';

const checkScript = async (index: number) => {
    try {
        const _userId = sessionStorage.getItem('@pionira/userId');
        const { data } = await api.get("/finalcheetahquiz");

        if (data[index].user_id.includes(_userId)) {
            return true;
        }
        else {
            return false;
        }
    } catch (error) {
        console.log(error);
    }
}

const trail1FinalQuiz = async () => {
	const script = await (checkScript(0) || checkScript(1) || checkScript(2)) ? [
		{
			name: 'Cheetah',
			image: cheetah,
			texts: [
				'Ol novamente, filhote',
				'Ento voc acha que est preparado para o nosso desafio final?',
				'Espero que voc tenha se preparado bem desde a ltima vez... Agora precisamos mais do que nunca da sua agilidade',
				'Aqui voc ir ser testado ao seu mximo, filhote Por isso, no poupe esforos em usar tudo o que aprendeu ao longo de sua jornada!',
				'Boa sorte dessa vez!',
			],
		},
	] : [
		{
			name: 'Cheetah',
			image: cheetah,
			texts: [
				'Olha s Ento voc acha que est apto para encarar o nosso desafio final, filhote?',
				'Espero que voc tenha se preparado bem desde os seus primeiros passos...',
				'Ainda temos cho pela frente! E bem... Agora precisamos mais do que nunca da sua agilidade',
				'Aqui voc ir ser testado ao seu mximo, filhote Por isso, no poupe esforos em usar tudo o que aprendeu ao longo de sua jornada!',
				'Boa sorte!',
			],
		},
	];

	return script;
};

export default trail1FinalQuiz;
