import lion_happy from "../../../assets/sprites/lion/happy_lion.png";
import lion from "../../../assets/sprites/lion/lion.png";
import lioness_happy from "../../../assets/sprites/lion/happy_lioness.png";

import api from '../../../services/api';

const getUser = async () => {
    const _userId = sessionStorage.getItem('@pionira/userId');
    const res = await api.get(`/user/${_userId}`);

    return res.data.userName;

}


const trail2Conclusion = async () => {

    const userName = await getUser();
    const script = [
        {
            name: "Leoa",
            image: lioness_happy,
            texts: [`Parabns, ${userName}. Voc conseguiu!`]
        },
        {
            name: "Leo",
            image: lion_happy,
            texts: ["Voc nos superou e nos orgulhou com a sua evoluo!"]
        },
        {
            name: "Leoa",
            image: lioness_happy,
            texts: ["Voc nos mostrou que consegue ser um lder! Mostrou ter o orgulho e a fora necessria para liderar o combate  Ignorncia!"]
        },
        {
            name: "Leo",
            image: lion,
            texts: ["Mas mantenha em mente que a batalha ainda no acabou e ainda pode haver derrotas. Mas nunca abaixe a cabea, continue sempre em frente!"]
        },
        {
            name: "Leoa",
            image: lioness_happy,
            texts: ["Aqui est a insgnia da Liderana, sempre que olhar para ela lembre-se de tudo que passou e todos os obstculos superados. Assim a Ignorncia no ter nenhuma chance!"]
        }
    ];

    return script;
}

export default trail2Conclusion;
