import blackMamba from "../../../assets/sprites/blackMamba/mamba_negra.png";
import mamba_negra_happy from "../../../assets/sprites/blackMamba/mamba_negra_happy.png";

import api from '../../../services/api';

const getUser = async () => {
    try {
        const _userId = sessionStorage.getItem('@pionira/userId');
        const res = await api.get(`/user/${_userId}`);

        return res.data;
    } catch (error) {
        console.log(error);
    }
}

const checkScript = async (index: number) => {
    try {
        const _userId = sessionStorage.getItem('@pionira/userId');
        const { data } = await api.get("/finalQuiz");
        if (data[index].user_id.includes(_userId)) {
            return true;
        }
        else {
            return false;
        }
    } catch (error) {
        console.log(error);
    }
}

const blackMambaScript = async () => {

    const user = await getUser();

    const script = user.finalQuizComplete.blackMamba ? [
        {
            name: "Mamba Negra",
            image: mamba_negra_happy,
            texts: [
                `Ora ora, ${user.userName}!`,
                "O que voc est fazendo aqui? Voc j conseguiu nos ajudar com a ignorncia!",
                "Obrigado pela preocupao, mas por enquanto no precisamos de voc! Pode seguir sua jornada!"
            ]
        }]
    :
    user.ignorance > 80 ? [await checkScript(2) ?
        {
            name: "Mamba Negra",
            image: blackMamba,
            texts: [
                "Ora ora, vejo que tem muita coragem mesmo!",
                "Ainda acredita que pode me derrotar mesmo depois da ltima vez?",
                "HA HA HA! Prepare-se!"
            ]
        } : {
            name: "Mamba Negra",
            image: blackMamba,
            texts: [
                "Ora ora, no esperava ser incomodado to cedo, viajante!",
                "Vejo que acredita ter o conhecimento necessrio para me desafiar.",
                "... sniff sniff",
                `Posso sentir que voc possui um nvel de ignorncia em ${user.ignorance}%, que precoce...`,
                "Acho que devo te dar uma lio! Talvez assim voc venha mais preparado da proxima vez!",
                "Prepare-se!"
            ]
        }

    ] : user.ignorance > 40 ? [await checkScript(1) ?
        {
            name: "Mamba Negra",
            image: mamba_negra_happy,
            texts: [
                "Ora ora, vejo que tem muita coragem mesmo!",
                "Ainda acredita que pode me derrotar mesmo depois da ltima vez?",
                "HA HA HA! Prepare-se!"
            ]
        } : {
            name: "Mamba Negra",
            image: blackMamba,
            texts: [
                `Ora ora, ${user.userName}!`,
                "Vejo que seguiu um pouco de sua jornada, entretanto ainda no  o suficiente.",
                "... sniff sniff",
                `Posso sentir que voc possui um nvel de ignorncia em ${user.ignorance}%, no  alto, mas no  o ideal...`,
                "Entretanto, vejo no seu olhar que deseja me desafiar... Meio displicente, talvez devo te ensinar uma lio...",
                "Prepare-se!"
            ]
        }
    ] : [await checkScript(0) ?
        {
            name: "Mamba Negra",
            image: mamba_negra_happy,
            texts: [
                "Ora ora, vejo que tem muita coragem mesmo!",
                "Ainda acredita que pode me derrotar mesmo depois da ltima vez?",
                "HA HA HA! Prepare-se!"
            ]
        } : {
            name: "Mamba Negra",
            image: blackMamba,
            texts: [
                `Ora Ora, seja bem-vindo, ${user.userName}!`,
                "Meus companheiros me informaram que voc teve bastante xito e suas jornadas.",
                "... sniff sniff",
                `Uau, posso sentir que voc possui um nvel de ignorncia em ${user.ignorance}%, isso  fascinante...`,
                "Vejo que possui a determinao e o conhecimento necessrio para me desafiar!",
                "Mas j vou lhe avisando que no sera nada fcil... Afinal, no  a toa que eu sou a rainha dessa savana!",
                "Prepare-se!"
            ]
        }
    ];
	
    return script;
}



export default blackMambaScript;
